html {
  font-size: 16px;
}


.App-header {
  background-color: #282c34;
  min-height: 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.App-link {
  color: #61dafb;
}

.table {
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	justify-content: space-evenly;
	align-items: stretch;
	align-content: center;
  margin: 0 5rem;
}

.table-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-items: flex-start;
  width: 80%;
}

input {
  width: 80%;
  text-align: center;
}